
#app{
  background:black;
  background-size: 100%;
  color:white;
  height: 100%;
}


*{
    margin:0px;
    padding:0px;
    list-style: none;
    border: none;
}
hr{
    border-top: 0.5px solid rgb(155, 154, 154);
    margin-bottom: 5px;
}
#header{
    background:rgb(37, 255, 255);
    height: 12%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: black;
}
#thismonth,.year{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
}
#thismonth{
    font-size: 30px;
}
#returnthisday{
    position: absolute;
    right: 0;
}
#left{
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
#returnthisday{
    float: right;
}

#themain {
  width: 100%;
  height: 65%;
  background: rgb(0, 89, 255);
  color: black;
}
ul {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
li {
  width: 14.1%;
  text-align: center;
  height: 7%;
  font-size: 1.2rem;
}
.add {
  position: absolute;
  right: 0;
}
.next{
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

#tail{
    background:rgba(0,0,0,0.5);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    color:white;
}
.today{
    font-size: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.todayweek{
    font-size: 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
